<template>
  <div class="content">
    <h1>Portfolio</h1>
    <input placeholder="Search Portfolio..." class="search" :value="textFilter" v-on:input="updateTextFilterQuery">

    <div v-if="tagFilters.length > 0">
      <p class="filterText">Tag filters (click to remove):</p>
      <ul class="tagFilters">
        <li v-on:click="removeTagFilter(tag)" v-for="(tag, tagIndex) in tagFilters" :key="tagIndex">
          <tag-pill :text="tag" :cross="true"/>
        </li>
      </ul>
    </div>
    <transition-group name="fade">
      <article-list-item
        v-on:tag-click="addTagFilter"
        v-for="(article, articleIndex) in filteredArticles"
        :key="articleIndex"
        :article="article"
        :index="articleIndex" />
    </transition-group>
  </div>
</template>

<script>
import ArticleListItem from '@/components/ArticleListItem.vue'
import TagPill from '@/components/TagPill.vue'
import { articles } from '@/fake_articles.json'

export default {
  components: {
    ArticleListItem,
    TagPill
  },

  data () {
    return {
      articles
    }
  },

  methods: {

    // adds the new tag to the URL of the site
    pushNewTagsToQuery (newTags) {
      this.$router.push({
        query: {
          ...this.$route.query,
          tags: newTags && newTags.length > 0 ? newTags.join(',') : undefined
        }
      })
    },

    // updates the site URL to the new tags
    updateTextFilterQuery ({ target: { value } }) {
      this.$router.push({
        query: {
          ...this.$route.query,
          filter: value || undefined
        }
      })
    },

    // add a new tag filter to the array
    addTagFilter (tag) {
      if (!this.tagFilters.includes(tag)) {
        const newFilters = [...this.tagFilters, tag]
        this.pushNewTagsToQuery(newFilters)
      }
    },

    // remove a tag filter from the array
    removeTagFilter (tag) {
      const newFilters = this.tagFilters.filter(el => el !== tag)
      this.pushNewTagsToQuery(newFilters)
    },

    getArticleURL (id) {
      return `/article/${id}`
    }
  },

  computed: {

    filteredArticles () {
      const { textFilter, tagFilters } = this
      const lowerCaseTagFilters = tagFilters.map(x => x.toLowerCase())

      // finds all the matches in a single article
      function findMatches (article) {
        const matches = []

        // the props to search
        const props = ['title', 'subtitle']

        // adds a match to the array of matches
        function addMatch (prop, keyword, index, length = keyword.length) {
          // this function checks if a match collides with another one.
          // I.e. if you're seracing the word "robot" and your keywords are "b" and "obo" the b won't collide
          function doesCollide (match) {
            if (match.index < index && match.index + match.length > index) {
              return true
            }
            if (match.index >= index && match.index <= index + length) {
              return true
            }

            return false
          }

          // if any of the matches collide, don't add this match
          if (matches.filter(m => m.prop === prop).some(doesCollide)) {
            return
          }

          // add it to the array
          matches.push({ prop, keyword, index, length })
        }

        // get all the keywords
        // makes them lowercase, and filters out whitespace
        // also sorts them in terms of length so that our filtering of collisions will prioritise longer matches
        const keywords = textFilter.toLowerCase().split(' ').filter(t => !!t && t !== ' ').sort((a, b) => b.length - a.length)

        // if there isn't a filter, then we'll just return everything as is
        if (!textFilter) {
          return { matches, article }
        }

        // we're going to serach in each of the properties we specified
        props.forEach(prop => {
          // grab the value
          const value = article[prop]

          // iterate over the keywords
          keywords.forEach(keyword => {
            // create a regex object based on the users search filter
            const regexStr = keyword.replace(/[|\\{}()[\]^$+*?.]/g, '\\$&')
            const regex = new RegExp(regexStr, 'gi')

            // storing our matches here
            const regexMatches = []

            // keep adding matches until there are no more
            for (let match = regex.exec(value); match !== null; match = regex.exec(value)) {
              regexMatches.push(regex.lastIndex)
            }

            // add all of our matches to our array
            regexMatches
              .forEach(matchIndex => addMatch(prop, keyword, matchIndex - keyword.length))
          })
        })

        // return it back
        return {
          matches,
          article
        }
      }

      return this.articles
        .filter(article =>
          // Allow through if we have no tag filters, or if we do, allow through if the article has at least 1 tag match
          tagFilters.length === 0 || lowerCaseTagFilters.every(tag => article.tags.map(x => x.toLowerCase()).includes(tag))
        )
        .map(findMatches) // process our array of articles articles matched by our serach results
        .filter(matchResult => !textFilter || matchResult.matches.length > 0) // show all results if there's no filter, or filter out articles that have no matches
        .sort((a, b) => b.matches.length - a.matches.length) // sort articles by the articles with most matches fisrt
        .map(result => ({ ...result.article, matches: result.matches })) // transform the results into an oject comining the article and search results
    },

    // the current text filters
    textFilter () {
      return this.$route.query.filter || ''
    },

    // the currently applied tag filters
    tagFilters () {
      const { tags } = this.$route.query
      return tags ? tags.split(',') : []
    }
  }
}
</script>

